:root {
  --pf-chart-global--FontFamily: $font-family-base;
  --pf-global--FontSize--md: $font-size-base;
  --pf-global--FontSize--sm: 13px;
}

.odc-resource-icon {
  font-size: 80%;
  fill: #2b9af3;
}

.odc-resource-icon text {
  fill: #fff !important;
}

.odc-resource-icon-service {
  fill: #6ca100;
}

.odc-resource-icon-deployment,
.odc-resource-icon-daemonset,
.odc-resource-icon-deploymentconfig {
  fill: #004080;
}

.odc-resource-icon-application {
  fill: #467f40;
}

