.odc-application-group {
  outline: none;
  cursor: pointer;
}

.odc-m-drag-active .odc-application-group {
  pointer-events: none;
}

.odc-application-group__bg {
  fill: var(--pf-global--Color--light-100);
  fill-opacity: 0.6;
  stroke: var(--pf-global--Color--light-100);
  stroke-width: 5px;
}

.odc-application-group.is-filtered .odc-application-group__bg {
  stroke: #ef9234;
}

.odc-application-group.is-selected .odc-application-group__bg {
  fill: #A5C4E2;
  stroke: #06c;
  fill-opacity: 0.5;
}

.odc-application-group.is-highlight .odc-application-group__bg {
  fill: #f5f5f5;
  stroke: #73bcf7;
}

.odc-application-group.is-dropTarget .odc-application-group__bg {
  fill: #A5C4E2;
  stroke: #73bcf7;
}

.odc-application-group__label {
  fill: var(--pf-global--Color--300);
}

.odc-application-group__label text {
  fill: var(--pf-global--Color--light-100);
  pointer-events: none;
  font-size: var(--pf-global--FontSize--sm);
}

.odc-m-drag-active .odc-application-group,
.odc-m-filter-active .odc-application-group {
  opacity: .4;
}

.odc-m-drag-active .odc-application-group.is-highlight,
.odc-m-drag-active .odc-application-group.is-dragging,
.odc-m-filter-active .odc-application-group.is-highlight,
.odc-m-filter-active .odc-application-group.is-dragging {
  opacity: 1;
}

.odc-m-filter-active:not(.odc-m-drag-active) .odc-application-group.is-filtered {
  opacity: 1;
}

