.odc-base-node {
  outline: none;
  cursor: pointer;
}

.odc-m-drag-active .odc-base-node {
  pointer-events: none;
}

.odc-base-node:focus {
  outline: none;
}

.odc-base-node__bg {
  fill: var(--pf-global--BackgroundColor--light-100);
}

.odc-base-node.is-filtered .odc-base-node__bg {
  stroke-width: 2px;
  stroke: #ef9234;
}

.odc-base-node.is-selected .odc-base-node__bg {
  stroke-width: 2px;
  stroke: #06c;
}

.odc-base-node.is-highlight .odc-base-node__bg {
  stroke-width: 0;
}

.odc-base-node.is-dropTarget .odc-base-node__bg {
  stroke-width: 4px;
  stroke: #73bcf7;
}

.odc-base-node__label {
  fill: var(--pf-global--Color--light-100);
  font-size: var(--pf-global--FontSize--md);
}

.odc-base-node__label text {
  pointer-events: none;
  fill: var(--pf-global--Color--300);
}

.odc-m-drag-active .odc-base-node,
.odc-m-filter-active .odc-base-node {
  opacity: .4;
}

.odc-m-drag-active .odc-base-node.is-dragging,
.odc-m-drag-active .odc-base-node.is-highlight,
.odc-m-filter-active .odc-base-node.is-dragging,
.odc-m-filter-active .odc-base-node.is-highlight {
  opacity: 1;
}

.odc-m-filter-active:not(.odc-m-drag-active) .odc-base-node.is-filtered {
  opacity: 1;
}

